.logo {
	display: flex;
	align-items: center;

	img {
		margin-right: 16px;
		width: 40px;
		height: 40px;
	}

}

.title {
	display: flex;
	flex-direction: column;

	strong {
		margin-bottom: 2px;
		font-size: 20px;
	}

	span {
		color: #9d9d9d;
	}

	@media (max-width: 525px){
		display: none;
	}
}